.App {
  text-align: center;
  width: 100%;
  height: 100%;
  position: fixed;
  display: block;
}
.title {
  background-color: #282c34;
  color: darkorange;
  font-size: calc(10px + 2vmin);
  align-items: center;
  height: 5vmin;
}

.button { 
  margin-right: 1vh;
  cursor: pointer;
  transition: all 0.3s ease;
  background: transparent;
}

.bt1 {
  border-radius: 3;
  color: #000;
}
.bt1:after {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  direction: rtl;
  z-index: -1;
  transition: all 0.3s ease;
}
.bt1:hover {
  color: #000;
}
.bt1:hover:after {
  left: auto;
  right: 0;
  width: 100%;
}
.bt1:active {
  top: 2px;
}

.gif {
  height: 40vmin;
  border-bottom: 4;
}

.main {
  box-sizing: border-box;
  margin: auto;
  font-size: 17px;
  padding-top: 7vmin;
}

.mainH {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: #3b3f49;
}

.footer {
  padding: 2vmin;
  color: #3b3f49;
  background-color: #282c34;
  font-size: 20px;
  bottom: 0;
  position: absolute;
  width: 100%;
}